.modal{
    width: 100%;
    height: 100vh;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) scale(0);
    // animation: quitar .4s infinite;
    position: fixed;
    z-index: 99;
    background: rgba(0, 0, 0, 0.45);
    backdrop-filter: blur(5px);
    display: grid;
    place-items: center;
    transition: .3s all;
    div{
        width: 95%;
        max-width: 400px;
        margin: 0 auto;
        background: white;
        border-radius: 25px;
        padding: 15px 0;
        h2{
            text-align: center;
        }
        picture{
            display: grid;
            place-items: center;
            img{
                border-radius: 15px;
                width: 90%;
            }
        }
        nav{
            width: 90%;
            margin: 0 auto;
            padding-top: 20px;
            border-top: 2px solid #aaa;
            display: flex;
            justify-content: space-evenly;
            a{
                svg{
                    font-size: 25px;
                }
            }
        }
    }
}
.anim{
    transform: translate(-50%,-50%) scale(1);
}
@keyframes quitar {
    from{
        transform: translate(-50%,-50%) scale(0);
    }
    to{
        transform: translate(-50%,-50%) scale(1);
    }
    
}